export function formatCouncelorList(data) {
    const response = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            if(item && item.user && item.user.confirmed){
                response.push(formatClinician(item))
            }
            
        })
    } else {
        response.push(formatClinician(data))
    }
    return response
}

function formatClinician(item){
    return {
        name:item?.name,
        id:item?.id,
        label:item?.name
    }
}

export function formatTraumaList(data) {
    const response = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            response.push(getFormatedData(item))
        })
    } else {
        response.push(getFormatedData(data))
    }
    return response
}

function getFormatedData(item){
    return {
        traumaCode:item.attributes.trauma_code,
        traumaName:item.attributes.trauma_name,
        traumaId:item.id
    }
}


export function formatBarrierList(data) {
    const response = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            response.push(getFormatedBarrierData(item))
        })
    } else {
        response.push(getFormatedBarrierData(data))
    }
    return response
}

function getFormatedBarrierData(item){
    return {
        barrierName:item.barrier_name,
        barrierId:item.id
    }
}