import moment from 'moment'

const meta = {
    counsellorId: 'Center ID',
    name: 'State',
    firstName: 'Name',
    lastName: 'Phone',
    traumaCodes: 'Email',
    clients: 'Clients',
    sessions: 'Providers',
    sessionsHours: 'Devices',
    type: 'Type',
}

const deviceMeta = {
    // id: "ID",
    device_id: 'Device ID',
    device_type: 'Device Type',
    device_serial: 'Device Serial',
    mac_address: 'Mac Address',
    is_assigned: 'Is Assigned',
    status: 'Status',
    owned_by: 'Owned By',
    edit_action: 'Edit Action',
    device_action: 'Device Action',
}


const clientMeta = {
    client_id: "Client ID",
    clinician: 'Provider',
    name: "Record Name",
    gender_bio: "Birth Gender",
    gender_identified: "ID Gender",
    license:"License",
    device: "Device",
    appointment:"Appointment"
}

export function formatClientsList(data) {
    const response = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            response.push(formatClientData(item))
        })
        response['meta'] = clientMeta;
    } else {
        response.push(formatClientData(data))
    }
    return response
}

function getDeviceMeta(tab) {
    let obj = {};
    if(tab == 1){
        obj = deviceMeta;
    } else {
        obj = {
            mac_address: 'Mac Address',
            ip_address: 'IP Address',
            passphrase: 'Passphrase'
        }
    }
    return obj;
}

export function formatDevicesList(data, activeTab) {
    const response = [];
    if (Array.isArray(data)) {
        data.forEach((item) => {
            response.push(formatDeviceData(item, activeTab))
        })
        response['meta'] = getDeviceMeta(activeTab);
    } else {
        response.push(formatDeviceData(data, activeTab))
    }
    return response
}

function handleSession(item){
    let res = 'NotScheduled';
    const sessions = item?.attributes?.sessions?.data;
    if(sessions?.length){
        for(let i = 0;i < sessions.length;i++){
            let el = sessions[i];
            if(el?.attributes?.status === 'pending' || el?.attributes?.status === 'scheduled'){
                res = 'Scheduled';
            }
        }
    } else {
        res = 'NotScheduled';
    }
    return res;
}

function formatClientData(item) {
    const obj = {
        clientID: (item && item.id) || '',
        client_id: (item && item.attributes && item.attributes.client_id) || '',
        clinician_status:
            item &&
            item.attributes &&
            item.attributes.counsellor &&
            item.attributes?.counsellor?.data
                ? item.attributes?.counsellor?.data?.attributes?.name
                : 'Not Assigned',
        name: (item && item.attributes && item.attributes.name) || '',
        gender_bio:
            (item && item.attributes && item.attributes.gender_bio) || '',
        gender_identified:
            (item && item.attributes && item.attributes.gender_identified) ||
            '',
        license:
            item &&
            item.attributes &&
            item.attributes.license &&
            item.attributes.license.data
                ? 'Issued'
                : 'Not Issued',
        device_status:
            item &&
            item.attributes &&
            item.attributes.license &&
            item.attributes.license.data &&
            item.attributes.license.data.attributes &&
            item.attributes.license.data.attributes.device &&
            item.attributes.license.data.attributes.device.data &&
            item.attributes.license.data.attributes.device.data.id
                ? item.attributes.license.data.attributes.device.data.attributes.device_id
                : 'Not Assigned',
        appointment: handleSession(item)
    }

    return obj;
}


function getPassPhrase(item) {
    return (
        (item &&
            item.attributes &&
            item.attributes.licenses &&
            item.attributes.licenses.data &&
            item.attributes.licenses.data[item.attributes.licenses.data.length-1] &&
            item.attributes.licenses.data[item.attributes.licenses.data.length-1].attributes &&
            item.attributes.licenses.data[item.attributes.licenses.data.length-1].attributes.passphrase  &&
            item.attributes.licenses.data[item.attributes.licenses.data.length-1].attributes.passphrase.data &&
            item.attributes.licenses.data[item.attributes.licenses.data.length-1].attributes.passphrase.data.attributes && 
            item.attributes.licenses.data[item.attributes.licenses.data.length-1].attributes.passphrase.data.attributes.name
        ) ||
        ''
    )
}


function formatDeviceData(item, activeTab) {
    let obj = {};
    if(activeTab == 1) {
        obj = {
            // id: item.id,
            device_id: item.attributes.device_id,
            device_type: item.attributes.device_type,
            device_serial: item.attributes.device_serial,
            mac_address: item.attributes.mac_address,
            is_assigned: String(item.attributes.is_assigned),
            status: item.attributes.status,
            owned_by: item.attributes.owned_by,
            edit_action: `/devices/edit?id=${item.id}`,
            device_action: {
                deviceID: item.id,
                isAssigned: item.attributes.center && item.attributes.center.data ? true : false,
                center_name: item && item.attributes && item.attributes.center && item.attributes.center.data && item.attributes.center.data && item.attributes.center.data.attributes && item.attributes.center.data.attributes.name || '',
                owned_by: item.attributes.owned_by,
            },
        }
    } else {
        obj = {
            mac_address: item && item.attributes && item.attributes.mac_address || '',
            ip_address: item && item.attributes && item.attributes.ip_address || '',
            passphrase: getPassPhrase(item)
        }
    }
    return obj
}

const logoBaseUrl = 'http://localhost:1337'

export function getMetaData(data) {
    return data.pagination
}

export function formatCenters(data) {
    let response = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            response.push(formatCenterData(item))
        })
    } else {
        response = formatCenterData(data)
    }

    return response
}

function formatCenterData(item) {
    const obj = {
        id: item.id,
        centerId:item && item.user && item.user.username,
        name: item.name,
        email: item.email,
        phoneNumber: item.phone_number,
        firstName: item.first_name,
        logo: logoBaseUrl + item.logo_image,
        lastName: item.last_name,
        country: item.country,
        state: item.state,
        city: item.city,
        postalCode: item.postal_code,
        areaCode: item.area_code,
        centerType: item.center_type,
        verified: item.is_verified,
        address: getAddress(item.street_address1, item.street_address2),
        createdAt: moment(item.createdAt).format('DD MMMM, YYYY'),
    }

    return obj
}

function getAddress(address1, address2) {
    let fullAddress = address1 + ' ' + address2
    return fullAddress
}

export function formatClients(data) {
    let response = []
    if (Array.isArray(data)) {
        data.forEach((item) => {
            response.push(formatClientUserData(item));
        })
    } else {
        response = formatClientUserData(data);
    }

    return response
}

function formatClientUserData(item) {
    const obj = {
        id: item && item.id || '',
        name: item && item.name || '',
        client_id: item && item.client_id || '',
        gender_bio: item && item.gender_bio || '',
        gender_identified: item && item.gender_identified || '',
        centers: item && item.centers && item.centers.length || '0',
        counselors: item && item.counsellors && item.counsellors.length || '0',
        sessions: item && item.sessions && item.sessions.length || '0'
    };
    return obj;
}

