import React, { useRef } from 'react'
import {
    Box,
    Button,
    Modal,
    TextField,
    Typography,
} from '@mui/material'
import { ValidatorForm } from 'react-material-ui-form-validator'
import { updateClientTrauma } from 'app/services/clientService'
import Autocomplete from '@mui/material/Autocomplete'

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #fff',
    boxShadow: 24,
    p: 4,
    borderRadius:'15px'
}

export default function AssignClinician(props) {
    const [assignedClinician, setAssignedClinician] = React.useState('')
    const [initialValue, setInitialValue] = React.useState('')
    const [submit,setSubmit] = React.useState(false)

    const formEl = useRef()
    const handleSubmit = () => {
        setSubmit(true)
        if(assignedClinician){
            setSubmit(true)
        const options = {
            data: {
                counsellor: Number(assignedClinician.id),
            },
        }

        updateClientTrauma(props.currentClient.clientID, options)
            .then((resp) => {
                props.handleSubmit()
            })
            .catch((err) => {
                console.log('error', err)
            })
        }
    }
    
    return (
        <div>
            <Modal
                keepMounted
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >
                <Box sx={style}>
                    <ValidatorForm
                        ref={formEl}
                        onSubmit={handleSubmit}
                        onError={() => null}
                    >
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="h6"
                            component="h2"
                        >
                            {'Assign Provider'}
                        </Typography>
                        <Box sx={{ minWidth: 300 }}>
                            <Autocomplete
                                disablePortal
                                fullWidth
                                id="combo-box-demo"
                                options={props.row || []}
                                sx={{ width: '100%', padding: '12px 0px' }}
                                value={initialValue}
                                onChange={(event, newValue) => {
                                    setAssignedClinician(newValue)
                                }}
                                inputValue={initialValue}
                                onInputChange={(event, newInputValue) => {
                                    setInitialValue(newInputValue)
                                }}
                                isOptionEqualToValue={(option, value) => true}
                                renderInput={(params) => (
                                    <TextField
                                        {...params}
                                        label="Assign Provider*"
                                    />
                                )}
                            />
                        </Box>
                        {
                            !assignedClinician && submit &&
                            <span style={{color:"red"}}>This field is required</span>
                        }       
                        <Box
                            sx={{
                                display: 'table',
                                marginLeft: 'auto',
                                marginRight: '0px',
                            }}
                        >
                            <Button
                                color="error"
                                variant="contained"
                                onClick={props.handleClose}
                            >
                                Cancel
                            </Button>
                            <Button
                                sx={{ ml: 2 }}
                                type="submit"
                                variant="contained"
                            >
                                Assign
                            </Button>
                        </Box>
                    </ValidatorForm>
                </Box>
            </Modal>
        </div>
    )
}
