import { makeRequest } from './authenticationService';

export const fetchCenter = (page) => {
    // let url = `/api/centers?pagination[page]=${page}`;
    let url = '/api/centers';

    if(page){
        url += `?pagination[page]=${page}`;
    }

    return makeRequest('GET', url)
        .then((resp) => {
            return resp;
        })
        .then((json) => {
            return json;
        }); 
}

export const getAllCenters = () => {
    let url = '/api/centers?all=-1';

    return makeRequest('GET', url)
        .then((resp) => {
            return resp;
        })
        .then((json) => {
            return json;
        }); 
}

export const getCenterDetails = (id) => {
    let url = `/api/centers/${id}`;

    return makeRequest('GET', url, )
        .then((resp) => {
            return resp;
        })
        .then((json) => {
            return json;
        }); 
}


export const searchCenters = (data,page) => {
    let url = `/api/centers?search=${data}&pagination[page]=${page}`

    return makeRequest('GET',url)
    .then((resp) =>{
        return resp;
    })
    .catch((err)=>{
        return err;
    })
}

export const filterCenters = (data,page) => {
    let url = `/api/centers?filter=${data}&pagination[page]=${page}`

    return makeRequest('GET',url)
    .then((resp) =>{
        return resp;
    })
    .catch((err)=>{
        return err;
    })
}

export const findCenters = (searchData, filters, page) => {
    let url = `/api/centers?search=${searchData}&filter=${filters}&pagination[page]=${page}`;

    return makeRequest('GET',url)
        .then((resp) =>{
            return resp;
        })
        .catch((err)=>{
            return err;
        })
}