import React, { useRef, useState, useEffect} from 'react'
import MenuItem from '@mui/material/MenuItem'
import { styled } from '@mui/system'
import FormControl from '@mui/material/FormControl'
import { Box, Button, Modal, Typography } from '@mui/material'
import { SelectValidator } from 'react-material-ui-form-validator'
import { ValidatorForm } from 'react-material-ui-form-validator'
import { createLicense } from 'app/services/clientService'
import useAuth from 'app/hooks/useAuth'
import moment from 'moment'
import { getCenterDetails } from 'app/services/centerService'

const SelectField = styled(SelectValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
    marginTop: '8px',
}))

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #fff',
    boxShadow: 24,
    p: 4,
    borderRadius: '15px',
}

export default function IssueLisence(props) {
    const [state, setState] = useState({
        deviceType: '',
    })
    const [centerData,setCenterData] = useState({})
    const { user } = useAuth()

    const handleSave = (event) => {
        const options = {
            license: {
                data: {
                    license_type:
                        deviceType === 'Loaner' ? 'Limited' : 'Freehold',
                    client: props.currentClient.clientID,
                    center: user?.center?.id,
                    status: deviceType === 'BYOD' ? 'Used' : 'New',
                    publishedAt:
                        moment().format().split('T')[0] +
                        ' ' +
                        moment().format().split('T')[1].slice(0, 8),
                },
            },
            passphrase: {
                data: {
                    used: true,
                    publishedAt:
                            moment().format().split('T')[0] +
                            ' ' +
                            moment().format().split('T')[1].slice(0, 8),
                },
            },
            device: {
                data: {
                    center: user?.center?.id,
                    publishedAt:
                        moment().format().split('T')[0] +
                        ' ' +
                        moment().format().split('T')[1].slice(0, 8),
                },
            },
            device_type: deviceType,
            state_code: centerData?.state?.state_id,
        }
        createLicense(options)
            .then((res) => {
                if (res.status === 'success') {
                    props.handleSubmit(props.currentClient.clientID)
                }
            })
            .catch((err) => {
                console.log('error', err)
            })
    }

    const handleChange = (event) => {
        setState({
            ...state,
            [event.target.name]: event.target.value,
        })
    }
    const { deviceType } = state
    const formEl = useRef()

    useEffect(() => {
        if(user && user.center){
            getCenterDetails(user?.center?.id)
            .then((res)=>{
                setCenterData(res)
            })
            .catch(err => {
                console.log("error",err)
            })
            }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    return (
        <div>
            <Modal
                keepMounted
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >
                <Box sx={style}>
                    <ValidatorForm
                        ref={formEl}
                        onSubmit={handleSave}
                        onError={() => null}
                    >
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="h6"
                            component="h2"
                        >
                            {'Device / License type'}
                        </Typography>
                        <Box sx={{ minWidth: 300 }}>
                            <FormControl fullWidth>
                                <SelectField
                                    id="demo-simple-select"
                                    value={deviceType}
                                    label="Device / License Type*"
                                    onChange={handleChange}
                                    name="deviceType"
                                    validators={['required']}
                                    errorMessages={['This field is required']}
                                >
                                    <MenuItem value={'Loaner'}>Loaner</MenuItem>
                                    <MenuItem value={'BYOD'}>BYOD</MenuItem>
                                </SelectField>
                            </FormControl>
                        </Box>
                        <Box
                            sx={{
                                display: 'table',
                                marginLeft: 'auto',
                                marginRight: '0px',
                            }}
                        >
                            <Button
                                color="error"
                                variant="contained"
                                onClick={props.handleClose}
                            >
                                Cancel
                            </Button>
                            <Button
                                sx={{ ml: 2 }}
                                type="submit"
                                variant="contained"
                            >
                                Assign
                            </Button>
                        </Box>
                    </ValidatorForm>
                </Box>
            </Modal>
        </div>
    )
}
