import React, { useState, useEffect } from 'react';
import {
    Grid,
    Box,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Alert,
    Button
} from '@mui/material'
import TextField from '@mui/material/TextField';
import { useNavigate } from 'react-router-dom';
import SearchIcon from '@mui/icons-material/Search';
import { Breadcrumb } from 'app/components';
import { formatClientsList, getMetaData } from 'app/utils/center.list.formatter';
import {
    fetchClients,
    fetchClicians,
    fetchClient,
    findClients,
    getClientDetails
} from 'app/services/clientService';
import Loading from 'app/components/MatxLoading/MatxLoading';
import UsersCustomTable from 'app/components/UsersCustomTable/UsersCustomTable';
import './Clients.css';
import AssignClinician from 'app/components/AssignDialog/AssignClinician';
import useAuth from 'app/hooks/useAuth'
import Snackbar from '@mui/material/Snackbar';
import IssueLisence from 'app/components/AssignDialog/IssueLisence';
import AssignDevice from 'app/components/AssignDialog/AssignDevice';
import DeviceIssueConfirmDialog from 'app/components/AssignDialog/DeviceIssueConfirmDialog';
import { formatCouncelorList } from 'app/utils/client.list.formatter';
import SceduleAppointment from 'app/components/AssignDialog/SceduleAppointment';
import { useDispatch } from 'react-redux';
import { resetClient } from 'app/redux/actions/ClientIntakeActions';
import SessionModal from 'app/components/SessionModal/SessionModal';

const Clients = () => {
    const navigate = useNavigate();
    const {user} = useAuth()
    const dispatch = useDispatch();

    const [rows, setRows] = useState([]);
    const [loading, setLoading] = useState(false);
    const page = 1;
    const [pageDetails, setPageDetails] = useState({});
    const [searchResult, setSearchResult] = useState(false);
    const [filterResult, setFilterResult] = useState(false);
    const [searchData, setSearchData] = useState("");
    const [clinicians,setClinicians] = useState([])
    const [devices,setDevices] = useState([])
    const [type, setType] = useState({ 
        clinician: "All", 
        device: "All" 
    });
    const [open,setOpen] = useState(false)
    const [schedule,setSchedule] = useState(false)
    const [error,setError] = useState(false)
    const [currentClient,setCurrentClient] = useState({})
    const [currentClientDetails,setCurrentClientDetails] = useState({})
    const [currentLicense,setCurrentLicense] = useState({})
    const [addClinician,setAddClinician] = useState(false)
    const [success,setSuccess] = useState(false)
    const [issueLicense,setIssueLicense] = useState(false)
    const [assignDevice,setAssignDevice] = useState(false)
    const [assignDeviceSuccess,setAssignDeviceSuccess] = useState(false)
    const [assignedDevice,setAssignedDevice] = useState("")
    const [openModal, setOpenModal] = useState(false);
    const [msg, setMsg] = useState('');

    console.log('');
    useEffect(() => {
        dispatch(resetClient())
        getAllClients(page);
        if( user && user.center && user.center.id){
            getAllClinicians(user?.center?.id)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [dispatch]);

    function getAllClinicians(id){
        setLoading(true);
        fetchClicians(id)
        .then(resp =>{
            setLoading(false);
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                if(resp && resp.counsellors){
                    setClinicians(formatCouncelorList(resp.counsellors));
                }
                if(resp && resp.devices){
                    setDevices(resp.devices);
                }
            }
        })
        .catch(err => {
            console.log("error",err)
        })
    }

    function getAllClients(pageNumber) {
        setLoading(true);
        fetchClients(pageNumber)
        .then(resp => {
            setLoading(false);
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                setRows(formatClientsList(resp.data));
                setPageDetails(getMetaData(resp.meta));
            }
        })
    }

    function sortClients(search, type, pageNo) {
        findClients(search, type, pageNo)
        .then(resp => {
            setLoading(false);
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                setRows(formatClientsList(resp.data));
                setPageDetails(getMetaData(resp.meta));
            }
        })
    } 

    const handleSearch = ({ target: { value }}) => {
        setSearchData(value);
        if(value && value.length > 2){
            setSearchResult(true);
            // searchClient(value, page);
            sortClients(value, type, page);
        } else if(value === "") {
            if(type.clinician === 'All' && type.device === 'All'){
                getAllClients(page);
                setSearchResult(false);
                setFilterResult(false);
            } else {
                setFilterResult(true);
                sortClients('', type, page);
            }
            
        }
    }


    const handleChange = ({ target: { value }}, filterType) => {
        if(filterType === 'clinician'){
            setType({ ...type, clinician: value});
        } else {
            setType({ ...type, device: value });
        }

        if(filterType === 'clinician'){
            if(value === 'All' && type.device === 'All'){
                if(searchData) {
                    setSearchResult(true);
                    sortClients(searchData, {clinician: 'All', device: 'All'}, page);
                } else {
                    setFilterResult(false);
                    getAllClients(page);
                }
            } else {
                setFilterResult(true);
                sortClients(searchData, {clinician: value, device: type.device }, page);
            }
        } else {
            if(type.clinician === 'All' && value === 'All'){
                if(searchData) {
                    setSearchResult(true);
                    sortClients(searchData, {clinician: 'All', device: 'All'}, page);
                } else {
                    setFilterResult(false);
                    getAllClients(page);
                }
            } else {
                setFilterResult(true);
                sortClients(searchData, { clinician: type.clinician, device: value }, page);
            }
        }
    }

    const handlePageChange = (event, newPage) => {
        if(searchResult || filterResult){
            sortClients(searchData, type, Number(newPage)+1);
        } else {
            getAllClients(newPage + 1);
        }
    }

    const onClick = (id) => {
        navigate(`/client/details/${id}`, {id: id});
    }

    const assignClinician = (row) => {
        if(clinicians && clinicians.length !== 0){
            setCurrentClient(row)
            setAddClinician(true)
        }else{
            setError(`No clinicians added to ${user?.center?.name}`)
            setOpen(true)
        }
    }

    const fetchCurrentLicense = (id) => {
        fetchClient(id)
        .then(resp => {
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                if(resp && resp.license){
                    setCurrentLicense(resp.license)
                    if(resp.license?.license_type === "Freehold"){
                        setAssignedDevice(resp.license.device)
                        setAssignDeviceSuccess(true)
                    }else{
                        let filteredDevice = devices.filter(item => !item.is_assigned && item.owned_by === "bmc")
                        if(filteredDevice && filteredDevice.length > 0){
                            setAssignDevice(true)
                        }else{
                            setError(`There are no available device for ${user?.center?.name}`)
                            setOpen(true)
                        }
                        
                    }
                    
                }
            }
        })
    }

    const issueLisence = (row) => {
        setCurrentClient(row)
        setIssueLicense(true)

    }

    const handleSubmit = () => {
        setAddClinician(false)
        setAssignDeviceSuccess(false)
        getAllClients(page);
        setIssueLicense(false)
    }

    const handleScheduleSubmit = () => {
        setSchedule(false)
        getAllClients(page);
        setSuccess(true)
    }
    const handleDeviceSubmit = (id,deviceType) => {
        getAllClients(page);
        setIssueLicense(false)
        fetchCurrentLicense(id)
    }

    const intakeDeviceSuccess = (assignedDevice) => {
        setAssignDevice(false)
        setAssignedDevice(assignedDevice)
        getAllClients(page);
        getAllClinicians(user?.center?.id)
        setAssignDeviceSuccess(true)
    }

    const scheduleSession = (row) => {
        getClientDetails(row.clientID)
        .then(resp => {
            if(resp?.error?.name === 'UnauthorizedError'){
                setOpenModal(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                if(!resp.counsellor){
                    setError(`Provider is not assigned`)
                    setOpen(true)
                }else{
                    if(resp.license && resp.license.device){
                        setCurrentClientDetails(resp)
                        setSchedule(true)
                    }else{
                        setError(`Device is not assigned`)
                        setOpen(true)
                    }
                    
                }
            }
            
        })
        
    }

    const issueDevices = (row) => {
        setAddClinician(false)
        if(devices && devices.length !== 0){
            setCurrentClient(row)
            if(row && row.license === "Issued"){
                fetchCurrentLicense(row.clientID)
            }
        }else{
            setError(`No devices added to ${user?.center?.name}`)
            setOpen(true)
        }
    }

    const handleReset = () => {
        setSearchData('');
        setType({
            clinician: 'All',
            device: 'All'
        })
        setSearchResult(false);
        setFilterResult(false);
        getAllClients(page);
        if(user && user.center){
            getAllClinicians(user?.center?.id)
        }
    }

    return (
        <Grid container className="client-container">
            <Grid className="client-breadcrumb">
                <Breadcrumb routeSegments={[{ name: 'All Clients' }]} />
            </Grid>

            <Grid className="client-box" sx={{ marginTop: '20px' }}>
                <TextField
                    label="Search Client"
                    variant="outlined"
                    className="client-search"
                    value={searchData}
                    onChange={handleSearch}
                    placeholder="Search client"
                    type="text"
                    InputProps={{
                        startAdornment: <SearchIcon />,
                    }}
                />

                <Box sx={{ minWidth: '40%' }} className="select-role">
                    <FormControl fullWidth>
                        <InputLabel
                            sx={{
                                backgroundColor: '#fafafa',
                                padding: '0.25rem',
                            }}
                        >
                            Provider Status
                        </InputLabel>
                        <Select
                            id="demo-simple-select"
                            value={type.clinician}
                            label="Age"
                            onChange={(e) => handleChange(e, 'clinician')}
                        >
                            <MenuItem value={'All'}>All</MenuItem>
                            <MenuItem value={'unassigned'}>Unassigned</MenuItem>
                            <MenuItem value={'assigned'}>Assigned</MenuItem>
                        </Select>
                    </FormControl>
                </Box>


            </Grid>

            <Grid className="client-box">
                <Box sx={{ minWidth: '40%' }}>
                    <FormControl fullWidth>
                        <InputLabel
                            sx={{
                                backgroundColor: '#fafafa',
                                padding: '0.25rem',
                            }}
                        >
                            Device Status
                        </InputLabel>
                        <Select
                            id="demo-simple-select"
                            value={type.device}
                            label="Age"
                            onChange={(e) => handleChange(e, 'device')}
                        >
                            <MenuItem value={'All'}>All</MenuItem>
                            <MenuItem value={'unassigned'}>Unassigned</MenuItem>
                            <MenuItem value={'assigned'}>Assigned</MenuItem>
                        </Select>
                    </FormControl>
                </Box>
            </Grid>

            <FormControl sx={{ marginBottom: '20px', display: 'flex', width: '100%' }}>
                <Button
                    color="primary"
                    variant='contained'
                    onClick={handleReset}
                    sx={{ alignSelf: 'flex-end'}}
                >
                    Reset
                </Button>
            </FormControl>

            {loading && <Loading />}

            {!loading && rows.meta && (
                <UsersCustomTable
                    onClick={onClick}
                    page={page}
                    pageDetails={pageDetails}
                    handlePageChange={handlePageChange}
                    rows={rows}
                    assignClinician={assignClinician}
                    issueLisence={issueLisence}
                    issueDevices={issueDevices}
                    scheduleSession={scheduleSession}
                    view={'client'}
                />
            )}

            <AssignClinician
                open={addClinician}
                row={clinicians}
                handleClose={() => setAddClinician(false)}
                handleSubmit={handleSubmit}
                currentClient={currentClient}
            />

            <IssueLisence
                open={issueLicense}
                handleClose={() => setIssueLicense(false)}
                handleSubmit={handleDeviceSubmit}
                currentClient={currentClient}
                issueDevice={issueDevices}
            />
            <AssignDevice
                open={assignDevice}
                row={devices}
                handleClose={() => setAssignDevice(false)}
                handleSubmit={intakeDeviceSuccess}
                clientId={currentClient.clientID}
                licenseId={currentLicense.id}
            />
            <DeviceIssueConfirmDialog
                open={assignDeviceSuccess}
                clientId={currentClient.client_id}
                license={currentLicense}
                handleClose={handleSubmit}
                deviceType={
                    currentLicense.license_type === "Freehold"
                        ? 'BYOD'
                        : assignedDevice.device_id +
                          '-' +
                          assignedDevice.device_type
                }
            />

            <SceduleAppointment
                open={schedule}
                handleClose={() => {setSchedule(false)}}  
                handleSubmit={handleScheduleSubmit}  
                row={currentClientDetails}
            />

            <Snackbar
                open={open}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                autoHideDuration={3000}
                onClose={() => {
                    setOpen(false)
                }}
            >
                <Alert
                    onClose={() => {
                        setOpen(false)
                    }}
                    variant="filled"
                    severity="error"
                    sx={{ width: '100%' }}
                >
                    {error}
                </Alert>
            </Snackbar>
            <Snackbar
                open={success}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                autoHideDuration={3000}
                onClose={() => {
                    setSuccess(false)
                }}
            >
                <Alert
                    onClose={() => {
                        setSuccess(false)
                    }}
                    variant="filled"
                    severity="success"
                    sx={{ width: '100%' }}
                >
                    Appoint has been scheduled
                </Alert>
            </Snackbar>
            <SessionModal
                open={openModal}
                text={msg}
                onConfirmDialogClose={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
                onYesClick={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
            />
        </Grid>
    )
}

export default Clients;