import React, { useRef } from 'react'
// import Dialog from '@mui/material/Dialog'
import MenuItem from '@mui/material/MenuItem'
import { styled } from '@mui/system'
import FormControl from '@mui/material/FormControl'
import {
    Box,
    Button,
    // DialogActions,
    // DialogContent,
    // DialogTitle,
    Modal,
    Typography,
} from '@mui/material'
import { SelectValidator } from 'react-material-ui-form-validator'
import { ValidatorForm } from 'react-material-ui-form-validator'
import { updateLicense } from 'app/services/clientService';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux'
import { resetClient } from 'app/redux/actions/ClientIntakeActions'
import SessionModal from 'app/components/SessionModal/SessionModal';

const SelectField = styled(SelectValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
    marginTop: '8px',
}))

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #fff',
    boxShadow: 24,
    p: 4,
    borderRadius:'15px'
}

export default function AssignDevice(props) {
    const navigate = useNavigate();
    const formEl = useRef();
    const dispatch = useDispatch();
    const [assignedDevice, setAssignedDevice] = React.useState('')
    const [expiry, setExpiry] = React.useState('')
    const [openModal, setOpenModal] = React.useState(false);
    const [msg, setMsg] = React.useState('');

    const handleChange = (event) => {
        setAssignedDevice(event.target.value)
    }

    const handleSubmit = () => {
        const options = {
            data: {
                device: Number(assignedDevice.id),
                clientId: props && props.clientId,
                action: 'Assign'
            },
        }
        
        updateLicense(props.licenseId, options)
            .then((resp) => {
                if(resp?.error?.name === 'UnauthorizedError'){
                    setOpenModal(true);
                    setMsg('Your session has expired. You will be logged out.');
                } else { 
                    props.handleSubmit(assignedDevice)
                    setAssignedDevice("")
                }
            })
            .catch((err) => {
                console.log('error', err)
            })
    }

    const handleClose = () => {
        setAssignedDevice("")
        props.handleClose()
    }

    
    return (
        <div>
            <Modal
                keepMounted
                open={props.open}
                onClose={props.handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >
                <Box sx={style}>
                    <ValidatorForm
                        ref={formEl}
                        onSubmit={handleSubmit}
                        onError={() => null}
                    >
                        <Typography
                            id="keep-mounted-modal-title"
                            variant="h6"
                            component="h2"
                        >
                            Assign Device
                        </Typography>
                        <Box sx={{ minWidth: 300 }}>
                            <FormControl fullWidth>
                                <SelectField
                                    id="demo-simple-select"
                                    value={assignedDevice}
                                    label="Device to Issue*"
                                    onChange={handleChange}
                                    name="state"
                                    validators={['required']}
                                    errorMessages={['This field is required']}
                                >
                                    <MenuItem value={''}>
                                        Device to Issue
                                    </MenuItem>
                                    {props.row &&
                                        props.row.filter(item => !item.is_assigned && item.owned_by === "bmc").map((item, i) => (
                                            <MenuItem
                                                value={item}
                                                key={i}
                                                disabled={item.is_assigned}
                                            >
                                                {item.device_id +
                                                    '-' +
                                                    item.device_type}
                                            </MenuItem>
                                        ))}
                                </SelectField>
                            </FormControl>
                        </Box>
                        {/* <Box sx={{ minWidth: 300 }}>
                            <FormControl fullWidth>
                                <SelectField
                                    id="demo-simple-select"
                                    value={expiry}
                                    label="Length of Issue"
                                    onChange={(e) => setExpiry(e.target.value)}
                                    name="state"
                                    validators={['required']}
                                    errorMessages={['This field is required']}
                                >
                                    <MenuItem value={''}>
                                        Length of Issue
                                    </MenuItem>
                                </SelectField>
                            </FormControl>
                        </Box> */}

                        <Box
                            sx={{
                                display: 'table',
                                marginLeft: 'auto',
                                marginRight: '0px',
                            }}
                        >
                            <Button
                                color="error"
                                variant="contained"
                                onClick={() => {
                                    dispatch(resetClient())
                                    handleClose()
                                }}
                            >
                                Cancel
                            </Button>
                            <Button
                                sx={{ ml: 2 }}
                                type="submit"
                                variant="contained"
                            >
                                Assign
                            </Button>
                        </Box>
                    </ValidatorForm>
                </Box>
            </Modal>
            <SessionModal
                open={openModal}
                text={msg}
                onConfirmDialogClose={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
                onYesClick={() => {
                    setMsg('');
                    setOpenModal(false);
                }}
            />
        </div>
    )
}
