import * as React from 'react'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { useNavigate } from 'react-router-dom'
import './UsersCustomTable.css'
import { TablePagination, IconButton, Badge } from '@mui/material'
import PreviewIcon from '@mui/icons-material/Preview';
import AddIcon from '@mui/icons-material/Add';
import VideocamIcon from '@mui/icons-material/Videocam';
import useAuth from 'app/hooks/useAuth';
import Button from '@mui/material/Button'

const _status = {
    'new': 'New',
    'inProcess': 'In Process',
    'successful': 'Successful',
    'unsuccessful': 'Unsuccessful',
    'pending': 'Pending',
    'scheduled': 'Scheduled',
    'rescheduled': 'Rescheduled',
    'cancelled': 'Cancelled',
    'completed': 'Completed'
}

const UsersCustomTable = (props) => {
    const navigate = useNavigate()
    const { user } = useAuth();

    const CustomHeadCell = ({ meta }) => {
        const data = [];
        if(meta){
        let entries = Object.entries(meta)
        for (const [index, [key, value]] of Object.entries(entries)) {
            if(key !== 'deviceId' && key !== 'counsellor_user_id'){
                data.push(<TableCell width={key === 'scheduledAt' ? 140 : 60} variant="head" align="center" className="text-style" key={index}>{value}</TableCell>)
            }
            
        }
    }
        return data
    }

    const showBtn = (value) => {
        if(value.isAssigned){
            return <div style={{marginLeft: '1rem'}}>{value.center_name}</div>;
        } else {
            if (user && user.role && user.role.type === 'admin') {
                if (value.owned_by === 'bmc') {
                    return (
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => props.handleOpen(value.deviceID, true)}
                        >
                            Assign
                        </Button>
                    );
                }
            }
        }

        return null;
    }

    const showClinicianBtn = (value,row) => {
        if(value !== 'Not Assigned') {
            return <div>{value}</div>
        } else {
            if(user && user.role && user.role.type === 'admin') {
                return <div>{'Not Assigned'}</div>
            } else if (user && user.role && user.role.type === 'center') {
                return (
                    <Button
                            variant="contained"
                            color="primary"
                            onClick={(e) => {
                                e.stopPropagation();
                                props.assignClinician(row)
                            }}
                        >
                        Assign
                    </Button>
                );
            }
        }
    }

    const showDeviceBtn = (value,row) => {
        if(value !== 'Not Assigned') {
            return <div>{value}</div>
        } else {
            if(user && user.role && user.role.type === 'admin') {
                return <div>{'Not Assigned'}</div>
            } else if (user && user.role && user.role.type === 'center') {
                return (
                    <Button
                            variant="contained"
                            color="primary"
                            onClick={(e) => {
                                e.stopPropagation();
                                props.issueDevices(row)
                            }}
                            disabled={row.license === "Not Issued"}
                        >
                        Assign
                    </Button>
                );
            }
        }
    }
    const showIssueBtn = (value,row) => {
        if(value === 'Issued') {
            return <div>{'Issued'}</div>
        } else {
            if(user && user.role && user.role.type === 'admin') {
                return <div>{'Not Issued'}</div>
            } else if (user && user.role && user.role.type === 'center') {
                return (
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={(e) => {
                            e.stopPropagation()
                            props.issueLisence(row)
                        }}
                    >
                        Issue
                    </Button>
                )
            }
        }
    }

    const showScheduleBtn = (value,row) => {
        if(value === 'Scheduled') {
            return <div>{'Scheduled'}</div>
        } else {
            if(user && user.role && user.role.type === 'admin') {
                return <div>{'Not Scheduled'}</div>
            } else if (user && user.role && user.role.type === 'center') {
        return (
            <Button
                variant="contained"
                color="primary"
                onClick={(e) => {
                    e.stopPropagation()
                    props.scheduleSession(row)
                }}
            >
                Schedule
            </Button>
        )
            }
            }
    }

    const viewNotes = (value) => {
        return (
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                {/* <Button
                    variant="contained"
                    color="primary"
                    onClick={() => props.handleOpen(value, true)}
                    startIcon={<VisibilityIcon />}
                >
                    Notes
                </Button> */}
                <IconButton
                    aria-label="view"
                    color="primary"
                    onClick={() => props.handleOpen(value, true)}
                ><Badge badgeContent={value.notes ? value.notes.length : 0} color="secondary">
                    <PreviewIcon/></Badge>
                </IconButton>
                <IconButton
                    aria-label="view"
                    color="primary"
                    onClick={() => props.handleOpenNewNotes(value, true)}
                >
                   <AddIcon />
                </IconButton>
            </div>
        );
    }

    const scheduleAppointment = (value) => {
        if(value.status == 'scheduled'){
            return <div style={{marginLeft: '1rem'}}>{value.scheduledAt}<IconButton aria-label="view" color="primary">{user.role.type === 'counsellor' && <VideocamIcon />}</IconButton></div>
                    // onClick={() => props.handleOpen(value, true)}
                    
        } else if(value.status == 'pending') {
            return (
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                <Button
                    variant="contained"
                    color="success"
                    style={{marginRight:5}}
                    onClick={() => props.handleAcceptModal(value, true)}
                    // startIcon={<VisibilityIcon />}
                >
                    <div style={{fontSize:'.8rem'}}>Accept</div>
                </Button>
                <Button
                    variant="contained"
                    color="error"
                    onClick={() => props.handleRejectModal(value, true)}
                    // startIcon={<VisibilityIcon />}
                >
                    <div style={{fontSize:'.8rem'}}>Reject</div>
                </Button>
                </div>
            )
        } else if(value.status == 'cancelled') {
            return <div style={{marginLeft: '1rem'}}>{`${value.scheduledAt} - Cancelled`}</div>
        }
        else {
            return <div style={{marginLeft: '1rem'}}>{value}<IconButton aria-label="view" color="primary">{user.role.type === 'counsellor' && <VideocamIcon />}</IconButton></div>
        }
        // if(value !== 'pending'){
        //     return <div style={{marginLeft: '1rem'}}>{value}<IconButton aria-label="view" color="primary">{user.role.type === 'counsellor' && <VideocamIcon />}</IconButton></div>
        //             // onClick={() => props.handleOpen(value, true)}
                    
        // } else {
        //     return (
        //         <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
        //         <Button
        //             variant="contained"
        //             color="success"
        //             style={{marginRight:5}}
        //             // onClick={() => props.handleOpen(value, true)}
        //             // startIcon={<VisibilityIcon />}
        //         >
        //             <div style={{fontSize:'.8rem'}}>Accept</div>
        //         </Button>
        //         <Button
        //             variant="contained"
        //             color="error"
        //             // onClick={() => props.handleOpen(value, true)}
        //             // startIcon={<VisibilityIcon />}
        //         >
        //             <div style={{fontSize:'.8rem'}}>Reject</div>
        //         </Button>
        //         </div>
        //     )
        // }
    }

    const showDisableBtn = (value, role) => {
        let content = null;
        content = (
            <Button
                variant="contained"
                color="primary"
                onClick={(e) => {
                    e.stopPropagation();
                    if(value.disabled){
                        props.handleEnable(role === 'center' ? value.center_id : value.provider_id);
                    } else {
                        props.handleDisable(role === 'center' ? value.center_id : value.provider_id);
                    }
                }}
            >
                {value.disabled ? 'Enable' : 'Disable'} 
            </Button>
        );
    
        return content;
    }

    const getCellContent = (key, value,row) => {
        let content = null;
        switch (key) {
            case 'edit_action':
                content = (
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={() => navigate(value)}
                    >
                        Edit
                    </Button>
                );
                break;
            case 'return':
            content = (
                <Button
                    variant="contained"
                    color="primary"
                    onClick={(e) => {
                        e.stopPropagation();
                        props.handleReturn(row)
                    }}
                    disabled={!value}
                >
                    Return
                </Button>
            );
            break;
            case 'expire':
            content = (
                <Button
                    variant="contained"
                    color="primary"
                    onClick={(e) => {
                        e.stopPropagation();
                        props.handleExpire(row);
                    }}
                    disabled={!value}
                >
                    Expire
                </Button>
            );
            break;
            case 'device_action':
                content = showBtn(value);
                break;
            case 'view_notes':
                content = viewNotes(value);
                break;
            case 'clinician_status':
                content = showClinicianBtn(value,row);
                break;
            case 'device_status':
                content = showDeviceBtn(value,row);
                break;
            case 'license':
                content = showIssueBtn(value,row);
                break;
            case 'scheduledAt':
                content = scheduleAppointment(value)
                break;
            case 'appointment':
                content = showScheduleBtn(value,row)
                break;
            case 'status': 
                content = _status[value] || value;
                break;
            case 'disable_center':
                content = showDisableBtn(value, 'center');
                break;
            case 'disable_provider': 
                content = showDisableBtn(value, 'provider');
                break;
            default:
                content = value;
                break;
        }

        return content;
    }

    const CustomBodyCell = ({ row }) => {
        const rows = [];
        let entries = Object.entries(row)
        for (const [index, [key, value]] of Object.entries(entries)) {
            if(key !== 'centerId' && key !== 'clientID' && key !== 'clinicId' && key !== 'deviceId' && key !== 'counsellor_user_id'){
            rows.push(
                <TableCell 
                    className="text-style" 
                    component="th" 
                    scope="row" 
                    align="center" 
                    key={index}
                >
                    {getCellContent(key, value,row)}
                </TableCell>
            )
            }

        }
        return rows
    }
    return (
        <>
            <Paper sx={{ width: '100%', overflow: 'hidden' }}>
                <TableContainer className="main-table">
                    <Table
                        sx={{ minWidth: 650 }}
                        size="small"
                        aria-label="simple table"
                        className="user-table"
                    >
                        <TableHead>
                            <TableRow>
                                <CustomHeadCell meta={props.rows.meta} />
                            </TableRow>
                        </TableHead>
                        {props.rows && props.rows.length > 0 ? (
                            <TableBody>
                                {(props.rowsPerPage > 0
                                    ? props.rows.slice(
                                          props.page * props.rowsPerPage,
                                          props.page * props.rowsPerPage +
                                              props.rowsPerPage
                                      )
                                    : props.rows
                                ).map((row,i) => {
                                    return (
                                        <TableRow
                                            key={i}
                                            sx={{
                                                '&:last-child td, &:last-child th':
                                                    {
                                                        border: 0,
                                                    },
                                            }}
                                            onClick={() => {
                                                props.onClick &&
                                                    props.onClick(
                                                        props.view === 'device'
                                                        ? row.deviceId
                                                        : props.view === 'center'
                                                            ? row.centerId
                                                            : props.view ===
                                                              'client'
                                                            ? row.clientID
                                                            : props.view ===
                                                              'counselor'
                                                            ? row.clientID
                                                            : props.view === 
                                                              'counsellor'
                                                            ? row.counsellor_user_id 
                                                            : props.view === 'clinician'?row.clinicId:row.id
                                                    )
                                            }}
                                            // onClick={() => navigate(`/${props.view}/details/${row.id}`, {id: row.id})}
                                        >
                                            <CustomBodyCell row={row} />
                                        </TableRow>
                                    )
                                })}
                            </TableBody>
                        ) : (
                            <>
                                {/* <TableRow className='no-records'> */}
                                <caption>No records found</caption>
                                {/* </TableRow> */}
                            </>
                        )}
                    </Table>
                </TableContainer>
                {props.view && (props.view === 'center' ||
                    props.view === 'device' ||
                    props.view === 'client' ||
                    props.view === 'counsellor' ||
                    props.view === 'clinician') ? (
                    <TablePagination
                        rowsPerPageOptions={[]}
                        component="div"
                        count={props.pageDetails.total || 0}
                        rowsPerPage={10}
                        page={props.pageDetails.page - 1 || 0}
                        onPageChange={props.handlePageChange}
                        onRowsPerPageChange={props.handleChangeRowsPerPage}
                        className="table-page"
                    />
                ) : (
                    <TablePagination
                        rowsPerPageOptions={[]}
                        component="div"
                        count={props.rows.length}
                        rowsPerPage={props.rowsPerPage || 10}
                        page={props.page || 0}
                        onPageChange={props.handleChangePage}
                        onRowsPerPageChange={props.handleChangeRowsPerPage}
                        className="table-page"
                    />
                )}
            </Paper>
        </>
    )
}

export default UsersCustomTable;